.weather-widget {
  background: linear-gradient(45deg, transparent, rgba(255,255,255,.5));
  padding: 1rem 2rem;
  border-radius: 5px;
  border: 1px solid white;
  filter: drop-shadow(10px 10px 10px rgb(19, 10, 75));
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 250px;
  font-family: 'Lora', sans-serif;
  &__form {
    bottom: -50px;
    position: absolute;
  }
  &__input {
    padding: .5rem;
    border: none;
    border-radius: 5px;
    width: 230px;
    outline: white;
  }
  &__city {
    font-size: 2em;
  }
  &__desc {
    font-size: .8em;
    opacity: .6;
  }
  &__temperature span {
    font-size: 3em;
    font-weight: bold;
  }
  p {
    margin: 0;
  }
}